import React, { useState, useEffect, Component } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { connect } from "react-redux";
import { MDBDataTable } from "mdbreact";
import NumberFormat from "react-number-format";

let mapStateToProps = (state) => {
    return {
        guid: state.auth.user.guid,
        sales: state.sales.map((sale) => {
            return Object.assign({}, sale);
        }),
    };
};

class SalesTable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
        };
    }

    render() {
        return this.props.sales == null ? (
            <h1>No dice</h1>
        ) : (
            <div>
                <MDBDataTable
                    name="salesTable"
                    id="salesTable"
                    entries={20}
                    striped
                    bordered
                    hover
                    data={{
                        columns: [
                            { label: "Date", field: "orderDate", sort: "asc", fixed: true },
                            { label: "Title", field: "title" },
                            { label: "SKU", field: "sku" },
                            { label: "TSIN", field: "tsin" },
                            // { label: "Price", field: "sellingPriceControl" },
                            { label: "Price (R)", field: "sellingPrice" },
                            { label: "Quantity", field: "quantity" },
                            { label: "Total (R)", field: "saleTotalControl" },
                            { label: "DC", field: "dc" },
                            { label: "customer", field: "customer" },
                        ],

                        rows: this.props.sales.map((sale) => {
                            sale.saleTotalControl = sale.sellingPrice * sale.quantity;
                            // (
                            //     <NumberFormat value={sale.sellingPrice * sale.quantity} displayType={"text"} thousandSeparator={true} prefix={"R "} />
                            // );

                            sale.sellingPriceControl = (
                                <NumberFormat value={sale.sellingPrice} displayType={"text"} thousandSeparator={true} prefix={"R "} />
                            );

                            return sale;
                        }),
                    }}
                    style={{ zIndex: 999999 }}
                />
            </div>
        );
    }
}

export default connect(mapStateToProps)(SalesTable);
