import axios from "axios";
import { Sale } from "../../models/SaleObject";

let apiUrlLocal = "http://localhost:4567/restapis/8o8l90bj7m/local/_user_request_";
let apiUrlJs = "https://yr2yl0lpui.execute-api.us-east-1.amazonaws.com/dev";
let apiUrlPy = "https://5habbbbms2.execute-api.us-east-1.amazonaws.com/dev";
let awsKey = "A8x8nBruJt5Obic2pzNdF3AtEzL4QOea6hN13D8A";

const guid = {
    get latest() {
        return JSON.parse(window.localStorage.getItem("price.beater")).auth.user.guid;
    },
};

const apikey = {
    get latest() {
        return JSON.parse(window.localStorage.getItem("price.beater")).auth.user.apikey;
    },
};

export async function getSales() {
    try {
        const response = await axios.get(apiUrlPy + "/sales?guid=" + guid.latest + "&apikey=" + apikey.latest, {
            headers: { "x-api-key": awsKey },
        });

        const sales = [];
        response.data.map((item, key) => {
            sales.push(new Sale(item));
        });

        return sales;
    } catch (error) {
        console.log(error);
        return null;
    }
}
