import * as types from "../constants/types.js";

export const storeAuth = (payload) => {
    return {
        type: types.STORE_AUTH,
        payload,
    };
};

export const storePageNumber = (payload) => {
    return {
        type: types.STORE_PAGE_NUMBER,
        payload,
    };
};

export const setRegistered = (payload) => {
    return {
        type: types.SET_REGISTERED,
        payload,
    };
};

// AUTH

export const login = (payload) => {
    return {
        type: types.LOGIN,
        payload,
    };
};

export const logout = () => {
    return {
        type: types.LOGOUT,
    };
};

export const isAuthenticated = () => {
    return {
        type: types.IS_AUTHENTICATED,
    };
};

// PRODUCTS

export const products_update = (payload) => {
    return {
        type: types.products_UPDATE,
        payload,
    };
};

export const products_set = (payload) => {
    return {
        type: types.products_SET,
        payload,
    };
};

export const products_clear = () => {
    return {
        type: types.products_CLEAR,
    };
};

// SALES

export const sales_set = (payload) => {
    return {
        type: types.sales_SET,
        payload,
    };
};

export const sales_clear = () => {
    return {
        type: types.sales_CLEAR,
    };
};
