export class Sale {
    constructor(json) {
        this.itemId = json.order_item_id;
        this.id = json.order_id;
        this.orderDate = json.order_date;
        this.saleStatus = json.sale_status;
        this.offerId = json.offer_id;
        this.tsin = json.tsin;
        this.sku = json.sku;
        this.title = json.product_title;
        this.url = json.takealot_url || json.takealot_url_mobi;
        this.sellingPrice = json.selling_price;
        this.quantity = json.quantity;
        this.dc = json.dc;
        this.customer = json.customer;
        this.shipmentId = json.shipment_id;
        this.poNumber = json.po_number;
        this.shipmentStateId = json.shipment_state_id;
        this.shipmentName = json.shipment_name;
        this.promotion = json.promotion;
    }
}

