import React, { useState, useEffect } from "react";
import { LineChart } from "react-chartkick";
import "chart.js";

export const all = (props) => {
    const sales = Object.assign([], props.sales);
    var dailyTotals = {};

    var runningTotal = {};
    var _runningTotal = 0;

    sales.reverse().forEach((sale) => {
        let dayTotal = sale.sellingPrice * sale.quantity;
        let dateSplit = sale.orderDate.split(" ");
        let saleDate = dateSplit[0] + " " + dateSplit[1] + " " + dateSplit[2];

        _runningTotal = _runningTotal + dayTotal;

        dailyTotals[saleDate] = (dailyTotals[saleDate] || 0) + dayTotal;
        runningTotal[saleDate] = _runningTotal;
    });

    return (
        <div>
            <LineChart
                prefix="R "
                thousands=","
                messages={{ empty: "No data (yet)" }}
                download={true}
                data={[
                    { name: "Daily Total", data: dailyTotals },
                    { name: "Running Total", data: runningTotal },
                ]}
            />
        </div>
    );
};
