import React, { useState, useEffect, Component } from "react";
import "@trendmicro/react-sidenav/dist/react-sidenav.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "../App.css";
import "react-tabs/style/react-tabs.css";
import { Link, Redirect, withRouter } from "react-router-dom";
import AppLayout from "../components/layouts/app";
import { connect, useDispatch } from "react-redux";
import { getSales } from "../helpers/sales/api";
import SalesTable from "../components/tables/salesTable";
import { sales_set as setSales } from "../state/actions";
import { CardRevenue30d, CardBestSelling30d, CardBestDay30d } from "../components/cards/CounterCards";

let mapStateToProps = (state) => {
    return {
        sales: state.sales,
    };
};

function mapDispatchToProps(dispatch) {
    return {};
}

const Sales = (props) => {
    const [processing, setProcessing] = useState(false);

    const dispatch = useDispatch();

    useEffect(() => {
        if (props.sales != null) return;
        getData();
    }, []);

    const getData = () => {
        setProcessing(true);
        getSales().then((sales) => {
            setProcessing(false);
            if (sales == null) return;
            dispatch(setSales(sales));
        });
    };

    const didSelectItem = () => {};

    return (
        <AppLayout>
            <div className="row justify-content-center">
                <div className="col-2">
                    <button type="button" class="btn btn-outline-info btn-sm" onClick={() => getData()}>
                        Refresh Results
                        {processing ? <i className="fa fa-spinner fa-spin" style={{ marginLeft: "5px" }}></i> : null}
                    </button>
                </div>
            </div>
            <div className="row justify-content-center" style={{ paddingTop: "30px" }}>
                <div className="col-10">
                    <div className="py-4">
                        <div class="card-deck">
                            <div class="card border-0">
                                <CardRevenue30d sales={props.sales} />
                            </div>
                            <div class="card border-0">
                                <CardBestSelling30d sales={props.sales} />
                            </div>
                            <div class="card border-0">
                                <CardBestDay30d sales={props.sales} />
                            </div>
                        </div>
                    </div>
                    <div className="shadow rounded p-5 mb-5">
                        <SalesTable sales={props.sales} didSelectItem={(sale) => didSelectItem()} />
                    </div>
                </div>
            </div>
            <div style={{ height: "50px" }}></div>
        </AppLayout>
    );
};
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Sales));
