import React, { useState, useEffect } from "react";
import AppLayout from "../components/layouts/app";
import { setRegistered } from "../state/actions/index";
import { connect } from "react-redux";
import SyncModal from "../components/modals/SyncModal";
import {
    CardTotal,
    CardTotalActive,
    CardTotalDeactive,
    CardWinning,
    CardLosing,
    CardRevenue30d,
    CardBestSelling30d,
    CardBestDay30d,
} from "../components/cards/CounterCards";
import { all as ChartSalesAll } from "../components/charts/salesCharts";

let mapStateToProps = (state) => {
    return {
        registered: state.global.registered,
        showModal: state.global.showModal,
        products: state.products,
        sales: state.sales,
    };
};

function mapDispatchToProps(dispatch) {
    return {
        setRegistered: (data) => dispatch(setRegistered(data)),
    };
}

const Dashboard = (props) => {
    const [showModal, setShowModal] = useState(props.showModal);
    useEffect(() => {
        // debugger;
        if (props.showModal == 1) {
            props.setRegistered({
                registered: props.registered,
                showModal: 0,
            });
        }
    }, []);

    const handleClose = () => {
        setShowModal(0);
    };

    const goToOffers = () => {};

    const goToSales = () => {};

    return (
        <AppLayout>
            <SyncModal show={showModal} handleClose={handleClose}>
                <div className="row justify-content-center">
                    <div className="col-10">
                        <h2>Welcome!</h2>
                        <br></br>
                        <h3>Your information is being pulled from Takealot. This might take up to 30 minutes.</h3>
                        <br></br>
                    </div>
                </div>
            </SyncModal>

            <div className="row justify-content-center" style={{ paddingTop: "30px" }}>
                <div className="col-10">
                    {/* Sales */}
                    <div className="shadow rounded p-5 mb-5">
                        <ChartSalesAll sales={props.sales}></ChartSalesAll>
                    </div>
                </div>
            </div>
            <div className="row justify-content-center" style={{ paddingTop: "30px" }}>
                <div className="col-10">
                    {/* Offers */}
                    <div className="shadow rounded p-5 mb-5">
                        <h2 className="pb-5">Products</h2>
                        <div class="card-deck">
                            <div
                                class="card border-0"
                                onClick={() => {
                                    goToOffers();
                                }}
                            >
                                <CardTotal products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    goToOffers();
                                }}
                            >
                                <CardTotalActive products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    goToOffers();
                                }}
                            >
                                <CardTotalDeactive products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    goToOffers();
                                }}
                            >
                                <CardWinning products={props.products} />
                            </div>
                            <div
                                class="card border-0"
                                onClick={() => {
                                    goToOffers();
                                }}
                            >
                                <CardLosing products={props.products} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </AppLayout>
    );
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
