export const CardRevenue30d = (props) => {
    var total = 0;
    props.sales.forEach((sale) => {
        total = total + sale.sellingPrice * sale.quantity;
    });

    return (
        <a className="card text-white bg-success mb-3 stretched-link">
            <div className="card-header">Total Revenue (30days)</div>
            <div className="card-body">
                <h5 className="card-title counter-total">
                    {<NumberFormat value={total} displayType={"text"} thousandSeparator={true} prefix={"R "} />}
                </h5>
            </div>
        </a>
    );
};

export const CardBestSelling30d = (props) => {
    var items = {};
    var bestSelling = "None";
    var quantity = 0;

    props.sales.forEach((sale) => {
        items[sale.title] = (items[sale.title] || 0) + 1;
    });

    Object.entries(items).forEach(([key, value]) => {
        if (value >= quantity) {
            bestSelling = value == quantity ? bestSelling + " & " + key : key;
            quantity = value;
        }
    });

    return (
        <a className="card text-white bg-primary mb-3 stretched-link">
            <div className="card-header">Best Selling (30days)</div>
            <div className="card-body">
                <div className="card-title counter-total">
                    <h3>
                        {bestSelling}
                        <br></br>
                    </h3>
                    <h5>{quantity + " sold"}</h5>
                </div>
            </div>
        </a>
    );
};

export const CardBestDay30d = (props) => {
    var items = {};
    var date = "None";
    var amount = 0;

    props.sales.forEach((sale) => {
        let dayTotal = sale.sellingPrice * sale.quantity;
        items[sale.orderDate] = (items[sale.orderDate] || 0) + dayTotal;
    });

    Object.entries(items).forEach(([key, value]) => {
        let dateSplit = key.split(" ");
        let saleDate = dateSplit[0] + " " + dateSplit[1] + " " + dateSplit[2];
        if (value >= amount) {
            date = saleDate == date ? saleDate + " & " + date : saleDate;
            amount = value;
        }
    });

    return (
        <a className="card text-white bg-info mb-3 stretched-link">
            <div className="card-header">Best Day (30days)</div>
            <div className="card-body">
                <div className="card-title counter-total">
                    <h2>
                        {date}
                        <br></br>
                        {<NumberFormat value={amount} displayType={"text"} thousandSeparator={true} prefix={"R "} />}
                    </h2>
                </div>
            </div>
        </a>
    );
};
